<template>
  <Suspense>
    <template #default>
      <ExampleTransfer :menu="teams"/>
    </template>
    <template #fallback>
      <LoaderComponentTwo/>
    </template>
  </Suspense>
</template>

<script>
import {reactive, onMounted, defineAsyncComponent} from "vue";
import EndPoints from "@/service/EndPoints";
import {handleError} from "@/components/composable/handleError";
const ExampleTransfer=defineAsyncComponent(()=>import('@/components/main/transfer/ExampleTransfer'))
const LoaderComponentTwo=defineAsyncComponent(()=>import('@/components/composable/LoaderComponentTwo'))
export default {
  name: "FranceLeage",
  components: {LoaderComponentTwo, ExampleTransfer},
  setup(){
    const teams=reactive([])
    const sum=onMounted(async ()=>{
      await EndPoints.getTransFranc()
          .then((response)=>{
            if(response.data.success){
              teams.push(response.data.data)
            }
          })
          .catch((error)=>{
           handleError(error)
          })
    })
    return{
      teams,sum,
    }
  }
}
</script>

